@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@400;600&display=swap);
body {
  margin: 0;
  font-family: "Source Sans Pro", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
}
h2,
h3 {
  text-align: center;
}
.trash {
  margin-top: 3vh;
  display: flex;
  flex-direction: column-reverse;
  background: #e2e2e2;
  width: 100px;
  border-radius: 10px;
  height: 200px;
  padding: 6px;
  box-shadow: 3px 3px 3px #efefef;
  margin-bottom: 4px;
}

.lds-ring {
  z-index: 10;
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
}
.lds-ring div {
  box-sizing: border-box;
  display: block;
  position: absolute;
  width: 64px;
  height: 64px;
  margin: 8px;
  border: 8px solid #fff;
  border-radius: 50%;
  -webkit-animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
          animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  border-color: #fff transparent transparent transparent;
}
.lds-ring div:nth-child(1) {
  -webkit-animation-delay: -0.45s;
          animation-delay: -0.45s;
}
.lds-ring div:nth-child(2) {
  -webkit-animation-delay: -0.3s;
          animation-delay: -0.3s;
}
.lds-ring div:nth-child(3) {
  -webkit-animation-delay: -0.15s;
          animation-delay: -0.15s;
}
@-webkit-keyframes lds-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
@keyframes lds-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.d-flex {
  display: flex;
}
.center {
  justify-content: center;
  align-items: center;
}
.side-nav {
  display: flex;
  align-items: center;
  flex-direction: column;
  width: 5vw;
  height: 100vh;
  background-color: #0466c8;
}
.main {
  display: flex;
  width: 100%;
}
.space {
  justify-content: space-around;
}
nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 95.2%;
  height: 60px;
  position: fixed;
  background-color: white;
  z-index: 10;
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.2);
}
.bi-house-fill {
  margin: 10px;
  margin-top: 20px;
}
.map {
  position: relative;
  width: 100%;
  height: 100%;
  background-image: url(/static/media/map.048e43c2.png);
  background-position: center;
  background-size: cover;
}
.sidenav {
  margin-top: 60px;
  padding-top: 25px;
  height: calc(100% - 150px); /* 100% Full-height */
  width: 500px; /* 0 width - change this with JavaScript */
  top: 60px; /* Stay at the top */
  left: 5vw;
  background-color: white; /* Black*/
}
.sidenav input {
  border-radius: 25px;
  padding-left: 10px;
  width: 250px;
  height: 25px;
  color: #127d92;
  font-size: 15px;
  border: 1px solid rgb(185, 185, 185);
}
.sidenav input:hover {
  outline: none;
}
table {
  margin-top: 25px;
  margin-left: 8px;
  margin-right: 8px;
  border-collapse: collapse;
  width: 100%;
}
.width-25 {
  width: 20%;
}
.text-right {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: right;
  color: #0466c8;
}
.status {
  width: 15px;
  height: 15px;
  border-radius: 8px;
  background-color: rgba(255, 0, 0, 0.534);
}
th,
td {
  text-align: left;
  padding: 8px;
}

.brand {
  margin-left: 10px;
  margin-right: 10px;
  font-weight: 600;
  font-size: 18px;
}
.line {
  display: block;
  width: 100%;
  height: 1px;
  background-color: #b4b4b4;
}
.bi-trash,
.bi-battery-charging {
  color: rgb(161, 161, 161);
  margin-left: 8px;
  margin-right: 8px;
}
.bi-trash::after {
  position: absolute;
  bottom: 0;
  right: 0;
  content: "";
  width: 25px;
  height: 25px;
  border-radius: 50%;
  background-color: #c72d2d;
}
.vertial-live {
  margin-left: 5px;
  margin-right: 10px;
  height: 28px;
  width: 2px;
  border-radius: 1px;
  background-color: #b4b4b4;
}
.city-picker {
  margin-right: 15px;
}
.trash-round {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 150px;
  left: 250px;
  width: 40px;
  height: 40px;
  background-color: #ffffff;
  border-radius: 27px;
  border: 2px solid #8f8f8f;
}

.popup {
  position: relative;
}

/* The actual popup */
.popup .popupcontent {
  visibility: visible;
  min-width: 220px;
  background-color: rgb(255, 255, 255);
  color: #000;
  text-align: center;
  border-radius: 6px;
  position: absolute;
  z-index: 1;
  top: -5%;
  left: 115%;
  box-shadow: 0 1px 20px 8px rgba(0, 0, 0, 0.1);
}

.popupcontent .popuptitle {
  display: flex;
  align-items: center;
  padding: 10px 15px;
  background-color: #e2e2e2;
  border-top-left-radius: 6px;
  border-top-right-radius: 6px;
  color: #000;
}

.popupcontent .popupmain {
}

.popupcontent .popupmain .popupmian-tile {
  padding: 10px 5px;
  display: flex;
  align-items: center;
  border-bottom: 1px solid rgb(230, 230, 230);
}
.popupcontent .popupmain .popupmian-tile:last-child {
  border-bottom: none;
}

